import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';

import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  public c59TO;
  public IDSTUDENT;
  public dbstudent;
  public student;
  public element;


  constructor(public db: AngularFireDatabase, public route: ActivatedRoute ) {

    this.IDSTUDENT = this.route.params.subscribe(params => {
      this.dbstudent = params['id'];
      console.log(this.dbstudent); // Print the parameter to the console.
  });

    this.db.list('STUDENT', ref => ref.orderByChild('ID').equalTo(this.dbstudent )).valueChanges().subscribe(data => {
      console.log(data);
      this.student = data [0];
    });

   }

  ngOnInit() {
  }

}
